import React, { useState, useEffect } from 'react';
import axios from 'axios';

function TestCo() {
    const [sheetsData, setSheetsData] = useState([]);

    useEffect(() => {
        // Effectue une requte GET pour rcuprer les donnes des feuilles depuis votre API
        axios.get('http://localhost:5038/backharmonistere/readSheetsData')
            .then(response => {
                setSheetsData(response.data); // Met  jour les donnes avec la rponse de l'API
            })
            .catch(error => {
                console.error('Erreur lors de la rcupration des donnes :', error);
            });
    }, []); // Excut une seule fois aprs le rendu initial

    return (
        <div>
            <h2>Donnes des feuilles :</h2>
            <ul>
                {sheetsData.map(sheet => (
                    <li key={sheet._id}>
                        {/* Affiche les diffrentes proprits de chaque feuille */}
                        <p>Titre : {sheet.pseudo}</p>
                        <p>Description : {sheet.email}</p>
                        {/* Ajoutez d'autres proprits ici */}
                    </li>
                ))}
            </ul>
        </div>
    );
}

export default TestCo;
